import {Component, ElementRef, HostListener, OnInit, ViewChild} from '@angular/core';
import {FotosInstagramService} from '../fotos-instagram.service';
import {LanavbarComponent} from '../lanavbar/lanavbar.component';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.scss']
})

export class InicioComponent implements OnInit {

  mostrarVerHistorias = false;
  listaHistorias = []
  listaFotos = []

  cargando = true;


  constructor(private fotoService:FotosInstagramService) {
  LanavbarComponent.paginaActual=false;
  }

  ngOnInit(): void {
    if(!localStorage.getItem('fotos')){
      this.fotoService.obtenerFotos().subscribe((d:any)=>{
        this.cargando = true
        this.listaFotos=d.fotos;
        this.listaHistorias=d.historias;
        localStorage.setItem('historias',JSON.stringify(this.listaHistorias))
        localStorage.setItem('fotos',JSON.stringify(this.listaFotos))
        this.cargando = false
      })
    }else{
      this.listaFotos = JSON.parse(localStorage.getItem('fotos'))
      this.cargando = false

    }


  }

  mostrarVer(){
    this.mostrarVerHistorias = !this.mostrarVerHistorias;
  }
  ocultarVer(){
    this.mostrarVerHistorias = !this.mostrarVerHistorias;
  }


}
